/* styles.css */

/* Add global styles here */

body {
  font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    min-height: 100vh; /* Make sure the body takes up at least the full height of the viewport */
  }
  
  header {
    
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    background-color: #1c1c1c;
    color: red;
    padding: 10px;
    text-align: center;
  }
  .header-btn{
    color: #AE445A;
  }
  
  header h1 {
    margin: 0;
  }
  
  
  nav {
    background-color: red;
    padding: 10px;
  }
  
  .tabs {
    display: flex;
    justify-content: space-around;
     /* Smooth transition for box-shadow */

  }
  
  .tab {
    background-color: #ccc;
    color: #f60031;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    transition: transform 0.25s ease-in-out;
  }
  
  .tab:hover {
    background-color: #bbb;
    transform: translateY(-5px);
  }
  
  main {
    padding: 20px;
    flex-grow: 1; /* Allow main content to grow to fill remaining space */
  }
  
  footer {
    background-color: #333;
    color: #fff;
    padding: 10px;
    text-align: center;
    margin-top: 500px; /* Push the footer to the bottom of the page */
  }
  